<template>
  <div id="activity" ref="contentWrapper">
      <div class="content-wrapper" @touchstart="refreshScrollOnce">
        <div class="header">
          <div class="member">
            <mu-icon-button icon="arrow_back" href="#/org/example"/>
            <div class="name">
              
            </div>
            <mu-icon-button icon="more_vert"/>
          </div>
        </div>
        <span class="notice">
          <img src="../assets/images/icon-xdf.png">
          <mu-raised-button label="+ " labelClass="label" class="demo-raised-button" blue/>
        </span>
        <span class="topic">90%off</span>
        <img src="../assets/images/off.png" class="off">
        <p class="text">
          -1.4524VIP90-<br>
          1.45<br>
          2.VIP90-<br>
          1.4511<br>
          2.VIP50-A<br>
          4511B1.100112.VIP30<br>
          -A
          1.45<br>
          2.VIP100<br>
          3.10050VIP
          B<br>
          1.45<br>
          2.VIP150<br>
        </p>
      </div>
  </div>
</template>

<script>
  import BScroll from 'better-scroll'
  import eventHub from 'assets/common/js/eventHub'

  export default {
    name: 'activity',
    data () {
      return {
        refreshed: false,
        startY: 0,
        endY: 0,
        showNavbar: true
      }
    },

    methods: {
      _initScroll () {
        //  BScroll ref  DOM 
        this.contentScroll = new BScroll(this.$refs.contentWrapper, {
          probeType: 3,
          click: true
        })
        this.refreshed = false

        this.contentScroll.on('beforeScrollStart', () => {
          this.startY = this.endY
        })

        this.contentScroll.on('touchend', pos => {
          this.endY = Math.round(pos.y)
          this.showNavbar = (this.endY - this.startY) > 0
          eventHub.$emit('nav.show', this.showNavbar)
        })
      },
      refreshScrollOnce () {
        if (!this.refreshed) {
          this.contentScroll.refresh()
          this.refreshed = true
        }
      }
    },
    created () {
      this.$nextTick(() => {
        this._initScroll()
      })
    }
  }
</script>

<style lang="sass">
  #activity
    max-height: 100vh
    overflow: hidden
    background-color: white
    z-index: 1
    .content-wrapper
      min-height: 102vh
      .header
        position: relative
        .member
          display: flex
          position: fixed
          top: 0
          left: 0
          width: 100%
          height: 50px
          z-index: 10
          color: #fff
          justify-content: space-between
          background: #00a2e7
        .mu-icon
          font-sizeL 20px
        .name
          margin-top: 10px
          font-size: 18px
      .notice
        position: absolute
        top: 50px
        left: 0
        height: 50px
        width: 100%
        padding: 5px
        background-color: #f2f2f2
        img
          position: absolute
          left: 10px
          height: 40px
          float: left
        .label
          font-size: 14px
        .demo-raised-button
          position: absolute
          height: 30px
          width: 40px
          top: 10px
          right: 10px
          color: white
          background-color: #00a2e7
          border-radius: 5px
      .topic
        position: absolute
        top: 110px
        left: 10px
        text-align: left
        font-size: 20px
      .off
        position: absolute
        top: 180px
        width: 94%
        height: auto
        left: 3%
      .text
        position: absolute
        top: 410px
        padding: 10px
        text-align: left
        font-size: 5px
</style>



